.call-nav {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-color: var(--pr);
	position: fixed;
	z-index: 9;
	top: 70px;
	right: 80px;
	font-size: 0;
	color: transparent;
	&::before,
	&::after {
		content: '';
		position: absolute;
		height: 0.125rem;
		width: 1.125rem;
		background-color: var(--wh);
		right: 0.75rem; }
	&::before {
		top: 1rem; }
	&::after {
		top: 1.4375rem;
		width: 0.9375rem; } }


$bem: '.nav';
.nav {
	position: absolute;
	width: 100%;
	padding: 4.75rem 0;
	& &__container {
		align-items: center; }
	& &__menu {
		font-size: 16px;
		text-transform: uppercase;
		margin-right: 3.125rem; }
	&-padding {
		padding-top: 11.25rem; } }
