 :root {
	--base-fz : 16px;
	--base-tc : #756B6B;
	--base-pl : #756B6B;

	--pr:   #EE665F;
	--pr-h: #EE6042;
	--gray: #756B6B;
	--gray-light: #EDEAEA;
	--wh: #fff;
	--bl: #000; }

*, ::before, ::after {
	box-sizing: border-box; }

body {
	font-family: 'Averta Demo PE';
	font-size: var(--base-fz);
	line-height: 1.52;
	color: var(--bl);
	font-weight: 400; }

.logo {
	position: relative;
	cursor: pointer;
	& &__part {
		--w: 9rem;
		--h: 1.5rem;
		color: var(--bl); }
	& &__detail {
		--w: 10.33px;
		--h: 15.06px;
		top: 0;
		left: 4.5rem;
		position: absolute; } }


svg {
	--w: 1.5rem;
	--h: 1.5rem;
	position: relative;
	width: var(--w);
	height: var(--h); }

a,
button,
.pointer,
.btn, {
	cursor: pointer;
	text-decoration: none; }


a,
a:focus,
a:focus-within,
a:active,
a:hover,
button,
button:focus,
button:focus-within,
.btn,
.btn:hover,
.btn:focus,
.btn:active {
	outline: none !important;
	border: none; }

a {
	color: var(--pr);
	&:active {
		color: var(--pr); }
	&:hover {
		color: var(--pr-h);
		text-decoration: none; } }

.d-flex {
	display: flex; }

.d-justify {
	justify-content: center; }

.d-justify-between {
	justify-content: space-between; }

.tt-uppercase {
	text-transform: uppercase; }

.animate::before,
.animate::after,
.animate {
	transition: all .25s liner; }

.text-limit span,
.text-limit {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	display: inline-block;
	max-width: 100%; }

.form-control::-webkit-input-placeholder,
.form-control::-moz-placeholder,
.form-control:-ms-input-placeholder,
.form-control::placeholder {
	color: $default-placeholder-color; }

.after,
.before {
	position: relative; }

.after::after,
.before::before {
	content: '';
	position: absolute; }


$fwList: 300 400 500 600 700 800;
@each $fontWeight in $fwList {
	.fw-#{$fontWeight} {
		font-weight: $fontWeight; } }

$fzList: 14px 16px 18px;
@each $fontSize in $fzList {
	.fz-#{$fontSize} {
		font-size: $fontSize; } }

.mb-20 {
	margin-bottom: 20px; }


.text-mutted {
	font-size: 1rem;
	color: var(--gray); }
